@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap");
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --main-clr: rgb(29, 29, 29);
  --dark-clr: rgb(10, 10, 10);
  --light-clr: rgb(228, 228, 228);
  --strong-clr: rgb(223, 94, 90);
  --weak-clr: rgb(150, 175, 230);
}

button,
textarea,
input,
select,
a {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

img {
  -o-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: "Inter", sans-serif;
  background: var(--light-clr);
  font-size: 1rem;
  min-width: 348px;
  scrollbar-width: thin;
  scrollbar-color: #aabed4 transparent;
}

html::-webkit-scrollbar,
body::-webkit-scrollbar {
  height: 0;
}

p,
h3,
h4,
h2,
label {
  line-height: 1.5em;
}

h3 {
  font-size: 1em;
}

button {
  font-family: "Inter", sans-serif;
}

.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  max-width: 80rem;
  margin: 0 auto;
  overflow: hidden;
  width: 100%;
}

.display-none {
  display: none;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #aabed4;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #7593b6;
}

@media (max-width: 65rem) {
  html,
  body {
    font-size: 0.9rem;
  }
}
